.index {
  &__main {
    margin: 24px;
    margin-bottom: 0;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
  }
  &__news {
    &-top {
      background: #1F1F23;
      border-radius: 4px;
      display: flex;
      align-items: center;
      padding: 8px;
      a {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        margin-right: 8px;
        &:hover,
        &:focus {
          svg path {
            fill: #C9C9CB;
          }
        }
      }
    }
    &-setting {
      margin-left: auto;
    }
    &-bottom {
      margin-top: 30px;
    }
    &-title {
      font-weight: 400;
      font-size: 13px;
      color: #AFAFB3;
      margin-bottom: 10px;
      a {
        font-weight: 500;
        font-size: 20px;
        color: #C9C9CB;
        margin-right: 20px;
        transition: .2s;
        &:hover,
        &:focus {
          color: #fff;
        }
      }
    }
    &-news {
      font-size: 13px;
      letter-spacing: 0.25px;
      color: #AFAFB3;
      margin-bottom: 8px;
      display: block;
    }
    &-time {
      margin-right: 8px;
      color: #797983;
    }
    &-comm {
      font-size: 12px;
      color: #797983;
      img {
        vertical-align: middle;
        margin-left: 10px;
        margin-right: 4px;
      }
      span {
        color: #AFAFB3;
        margin-left: 4px;
      }
    }
    &-more {
      font-size: 12px;
      color: #797983;
      display: flex;
      align-items: center;
      margin-top: 12px;
      transition: .2s;
      &:hover,
      &:focus {
        color: #AFAFB3;
        svg path {
          fill: #AFAFB3;
        }
      }
      svg {
        transform: rotate(-90deg);
        margin-left: 8px;
      }
      svg path {
        fill: #797983;
      }
    }
    &-list {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      margin-top: 35px;
    }
    &-item {
      width: 48%;
      margin-bottom: 24px;
    }
    &-link {
      display: flex;
      flex-direction: column;
      height: 100%;
    }
    &-img {
      border-radius: 6px;
      width: 100%;
    }
    &-name {
      font-weight: 500;
      font-size: 15px;
      line-height: 135%;
      letter-spacing: 0.25px;
      color: #C9C9CB;
      margin-top: 8px;
      margin-bottom: 4px;
    }
    &-stat {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 13px;
      letter-spacing: 0.25px;
      color: #797983;
      margin-top: auto;
      img {
        margin-left: 6px;
        margin-right: 4px;
      }
      span {
        font-size: 12px;
        display: flex;
        align-items: center;
      }
    }
    &-text-w {
      margin-left: 3px;
      color: #AFAFB3;
    }
    &-load {
      background: #1F1F23;
      border-radius: 4px;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 44px;
      font-size: 12px;
      letter-spacing: 0.3px;
      color: #AFAFB3;
      transition: .2s;
      &:hover,
      &:focus {
        color: #fff;
        svg path {
          fill: #fff;
        }
      }
      svg {
        transform: rotate(-90deg);
        margin-left: 8px;
      }
    }
  }
}

@media (max-width: 1100px) {
  .index {
    &__wrap {
      flex-wrap: wrap;
    }
    &__left-bar {
      order: 3;
      align-self: flex-start;
    }
    &__main {
      margin: 0;
      margin-top: 30px;
    }
    &__news {
      &-item {
        width: 32%;
      }
    }
  }
}

@media (max-width: 850px) {
  .index {
    &__news {
      &-item {
        width: 48%;
      }
    }
  }
}
@media (max-width: 665px) {
  .index {
    &__wrap {
      justify-content: center;
    }
    &__right-bar,
    &__left-bar {
      width: 100%;
    }
  }
}

@media (max-width: 500px) {
  .index {
    &__news {
      &-item {
        width: 100%;
      }
    }
  }
}