.forum {
  &__list {
    padding-bottom: 0;
  }
  &__link {
    &--arrow {
      padding: 10px 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      svg {
        transform: rotate(-90deg);
        path {
          fill: #797983;
        }
      }
      &:hover,
      &:focus {
        svg path {
          fill: #fff;
        }
      }
    }
  }
  &__block {
    img {
      margin-left: 8px;
      margin-right: 5px;
    }
  }
}