.memes {
  &__mem {
    img {
      width: 100%;
    }
  }
  &__user {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 20px;
    padding-bottom: 12px;
    .component-text-grey-13 {
      color: #797983;
    }
    .memes__text {
      img {
        margin: 0 5px;
      }
      &--w {
        color: #AFAFB3;
        margin-left: 4px;
      }
    }
    span {
      display: flex;
      align-items: center;
      img {
        margin-right: 8px;
      }
    }
  }
}