.component-block {
  background: #1F1F23;
  border-radius: 4px;
  margin-top: 24px;
  width: 300px;
  min-width: 300px;
  &__title {
    display: flex;
    font-weight: 500;
    font-size: 20px;
    padding: 14px 20px;
    transition: .2s;
    &:hover,
    &:focus {
      color: #fff;
    }
  }
  &__list {
    border-top: 1px solid #28282D;
    padding-bottom: 12px;
  }
  &__link {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    padding-right: 10px;
    &:hover,
    &:focus {
      background: linear-gradient(270deg, #28282D 0%, #1F1F23 100%);
    }
  }
  &__block {
    display: flex;
    flex-direction: column;
    border-top: 1px solid #28282D;
    width: 100%;
    margin-left: 8px;
  }
}

.component-text-grey-13 {
  font-size: 13px;
  color: #AFAFB3;
  letter-spacing: 0.25px;
}

.component-text-grey-11 {
  font-size: 11px;
  letter-spacing: 0.3px;
  color: #61616F
};

@media (max-width: 665px) {
  .component-block {
    width: 100%;
    min-width: 300px;
  }
}